import { printConstants } from "./constants";
// actions
export const loadingAction = () => ({
  type: printConstants.LOADING_PRINT,
});
export const loadingUpdatedAction = () => ({
  type: printConstants.LOADING_UPDATED,
});
export const getPrintSuccessAction = (data) => ({
  type: printConstants.GET_PRINT_SUCCESS,
  payload: data,
});
export const getPrintFailAction = (error) => ({
  type: printConstants.GET_PRINT_FAIL,
  payload: error,
});

export const createPrintSuccessAction = (data) => ({
  type: printConstants.CREATE_PRINT_SUCCESS,
  payload: data,
});

export const createPrintFailAction = (error) => ({
  type: printConstants.CREATE_PRINT_FAIL,
  payload: error,
});

export const deletePrintSuccessAction = (data) => ({
  type: printConstants.PRINT_DELETED_SUCCESS,
  payload: data,
});

export const deletePrintFailAction = (error) => ({
  type: printConstants.PRINT_DELETED_FAIL,
  payload: error,
});

export const updatedPrintSuccessAction = (data) => ({
  type: printConstants.PRINT_UPDATE_SUCCESS,
  payload: data,
});

export const updatedPrintFailAction = (error) => ({
  type: printConstants.PRINT_UPDATE_FAIL,
  payload: error,
});
